.NavBar {
    background-color: #ffffff;
    border-bottom: 1px solid #dadadf;
    padding: 0.5rem 1rem;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
    z-index: 1000;
  }
  
.NavBar img {
    margin-right: 0.5rem;
    vertical-align: middle;
  }

.brand {
    display: flex;
    align-items: center;
  }
  
.brandText {
    font-size: 1.5rem;
    font-weight: 600;
    margin-left: 0.5rem;
    color: #1478B5;
  }
.navLinks {
    display: flex;
    gap: 1.5rem;
    align-items: center;
  }
  
.navLink {
    color: #1478B5 !important;
    font-weight: 500 !important;
    transition: color 0.2s ease-in-out !important;
    cursor: pointer !important;
    padding: 5px;
  }
  
  .navLink:hover {
    color: #007bff !important;
  }
  
  .navLink i {
    margin-right: 0.4rem;
  }

  .Active {
    color: #007bff !important;
    font-weight: 600 !important;
  }
  
  .Active i {
    color: #007bff !important;
  }